
import { Options, Vue, setup } from 'vue-class-component';

import { useSpecialities, useConsultationInfo } from '@/composable';

import { RouteNames } from '@/router/types';
import { SpecialityDto } from '@/types/models';

import {
  RCard, RCardActions, RCardSection,
} from '@/ui/molecules';
import { RImg } from '@/ui/atoms';
import { TextRows } from '@/ui/organisms';

@Options({
  components: {
    RCard,
    RCardActions,
    RCardSection,
    RImg,
    TextRows,
  },
})
export default class SpecialitiesList extends Vue {
  specialities = setup(useSpecialities);

  consultationInfo = setup(useConsultationInfo);

  openDoctorsBySpecs(speciality: SpecialityDto): void {
    const { saveSpecialityToStoreAndBrowserStorage } = this.consultationInfo.consultationInfoStore;
    const { id, specialityName } = speciality;

    saveSpecialityToStoreAndBrowserStorage(speciality);

    this.$router.push({
      name: RouteNames.DOCTORS_BY_SPECS,
      params: {
        specialityId: id,
        specialityName,
      },
    });
  }
}
